import './player';
import './board';
var keyDown = function (e) {
    if (e.code === 'ArrowUp' || e.code === 'ArrowDown' || e.code === 'ArrowLeft' || e.code === 'ArrowRight') {
        logKey(e.code);
        if (player) {
            movement(player, e.code);
        }
    }
    function logKey(key) {
        console.log(key);
    }
    return;
};
var newPlayer = document.createElement('div');
newPlayer.classList.add('player');
document.querySelector('body').appendChild(newPlayer);
var player = document.querySelector('.player');
//TODO #2:
function movement(item, arrowCode) {
    var element = item.getBoundingClientRect();
    var x, y;
    if (element && x == undefined && y == undefined) {
        x = element.left;
        y = element.top;
    }
    // console.log( arrowCode)
    if (arrowCode === undefined || item === undefined)
        return;
    if (arrowCode === 'ArrowUp') {
        y = y - 16;
    }
    if (arrowCode === 'ArrowDown') {
        y = y + 16;
    }
    if (arrowCode === 'ArrowLeft') {
        x = x - 16;
    }
    if (arrowCode === 'ArrowRight') {
        x = x + 16;
    }
    if (x < 8 || y < 8) {
        return;
    }
    if (arrowCode === 'ArrowRight' || arrowCode === 'ArrowLeft') {
        item.style.left = "".concat(x, "px");
    }
    if (arrowCode === 'ArrowDown' || arrowCode === 'ArrowUp') {
        item.style.top = "".concat(y, "px");
    }
    console.log(x, y);
}
function makeSquares() {
    console.log(window.innerWidth, window.innerHeight);
    var boundaries = (window.innerWidth / 16) * (window.innerHeight / 16);
    for (var i = 0; i < boundaries; i++) {
        // console.log(i)
        var bod = document.querySelector('body');
        var square = document.createElement('div');
        square.classList.add('square');
        if (!bod)
            return;
        bod.appendChild(square);
    }
}
document.addEventListener('keydown', keyDown, false);
window.addEventListener('load', function () {
    makeSquares();
});
